* {
  box-sizing: border-box;
  font-family: sans-serif;
}

*:focus {
  outline: none;
}

html, body {
  width: 100%;
  display: flex;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

a {
  text-decoration: none;
  color: black;
}

button {
  border-width: 0;
}

div#root, div#root > div {
  width: 100%;
  height: 100%;
}

/* mdb react config */
.row {
  margin: 0;
}
.home-carousel, .home-list {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}

.home-event {
  height: 300px;
  padding: 20px;
}

.event-inner {
  width: 100%;
  height: 100%;
  background-color: white;
  box-shadow: 1px 1px 20px 0px #00000047;
}

.event-img-ctn {
  width: 100%;
  height: 50%;
  overflow: hidden;
}

.event-des-ctn {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  height: 50%;
  padding: 10px;
}

.event-img {
  width: 100%;
  border-bottom: 1px #eeeeeeee solid;
}

h3.event-des-title {
  font-size: 15px;
  font-weight: bold;
  flex: 1 1;
}

.event-des-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.event-des-place {
  font-size: 13px;
  border: 1px #7272724b solid;
  padding: 3px 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  align-self: flex-end;
}

.event-des-calendar {
  width: 70px;
  height: 70px;
  background-color: white;
  border: 1px #7272724b solid;
  box-shadow: 3px 3px 5px 0px #00000024;
}

.calendar-month {
  font-size: 10px;
  color: white;
  text-align: center;
  padding: 3px;
  height: 30%;
  background-color: #EB1212;
}

.calendar-date {
  font-size: 25px;
  text-align: center;
  height: 50%;
}

.calendar-wd {
  font-size: 10px;
  text-align: center;
  height: 20%;
}
